// 
function noMultipleClicks(methods,...args) {
    let that = this;
    
    if (that.noClick) {
		console.log("")
        that.noClick= false;
        methods(...args);
        setTimeout(function () {
            that.noClick= true;
        }, 2000)
    } else {
        console.log("")
    }
}

//
export default noMultipleClicks
