// loadMore mixin
export const loadMoreMixin = {
	data(){
		return{
			// 
			hasMoreData:true,
			// more  uni-load-more 
			moreStatus:{
				true: 'loading',
				false: 'noMore'
			}
		}
	},
	methods:{
		setHasMore(hasMoreData){
			this.hasMoreData = hasMoreData;
		},
		getHasMore(hasMoreData){
			return hasMoreData;
		}
	}
}

export default {
	loadMoreMixin:loadMoreMixin
}