// 
//  tabBar  tabBar 
export function switchTab(param = {
	url: ''
}) {
	uni.switchTab({
		url: param.url,
		success: param.success,
		fail: param.fail,
		complete: param.complete
	})
}
// 
export function reLaunch(param = {
	url: '',
	query: {}
}) {
	uni.reLaunch({
		url: param.url + formatUrl(param.url,param.query),
		success: param.success,
		fail: param.fail,
		complete: param.complete
	})
}
