//  tabbar  uni.navigateBack 
export function navigateTo(param = {
	url: '',
	query: {},
	events: {}
}) {
	const pages = getCurrentPages();
	if (pages.length >= 10) {
		redirectTo(param);
		return;
	}
	console.log(param.url + formatUrl(param.url,param.query))
	uni.navigateTo({
		url: param.url + formatUrl(param.url,param.query),
		events: param.events,
		success: param.success,
		fail: param.fail,
		complete: param.complete
	})
}
//  getCurrentPages 
export function navigateBack(param = {
	delta: 1
}) {
	uni.navigateBack({
		delta: param.delta,
		success: param.success,
		fail: param.fail,
		complete: param.complete
	})
}
//  URL
//  {a:1} => a=1
function formatUrl(initUrl = '',query = {}) {
	// 
	let firstCode = '?';
	if(initUrl.indexOf('?') > -1){
		if(initUrl.slice(-1) === '&'){
			firstCode = ''
		}
		else{
			firstCode = '&';
		}
	}
	const queryKeys = Object.keys(query);
	let url = '';
	if (queryKeys.length) {
		url = firstCode + queryKeys.map(keys => (keys + '=' + query[keys])).join('&');
	} else {
		url = ''
	}
	return url;
}
